.navbar {
  background-color: #f02516;
  background-image: linear-gradient(15deg, #f02516 0%, #fcca46 100%);
  font-size: 30px;
  font-weight: bold;
  height: 70px;
  width: 100%;
  box-shadow: 0px 0px 5px silver;
}

.toolbar {
  display: flex;
  min-height: 50px;
  padding: 0px 10px;
}

.title {
  margin: 15px 3px 3px 3px;
}
